<template>
  <group class="jq-mt">
    <popup-picker :title="title" :data="list" :value.sync="time"></popup-picker>
  </group>
  <group class="jq-mt">
    <div class="chart" v-ebarline="barLineData"></div>
  </group>
  <group class="jq-mt">
    <div class="chart" v-ebarline="barLineDataSMD"></div>
  </group>
  <group class="jq-mt">
    <div class="chart" v-ebarline="barLineDataADD"></div>
  </group>
</template>
<script>
  import { PopupPicker, Group,Cell} from 'vux/src/components'
  import { getList } from '../../../untils/api'

  export default {
    components: {
      PopupPicker,
      Group,
      Cell
    },
    data () {
      return {
        msg: '利润与产值',
        title: '日期',
        time: formatData.defaultTimeYear,
        barLineData:{},
        barLineDataSMD:{},
        barLineDataADD:{},
        list: formatData.dateListYear
      }
    },
    computed: {

    },
    ready(){
      document.title = this.msg;
    },
    methods: {
      async getBarLineData() {
          commit('UPDATE_LOADING', true)
      let id = "APP10101"
       let date=formatData.dateConversion(this.time);
      let dataTit = await getList(id,{date_:date});
        if(dataTit.success){
          //表头：
          //表格:
          const TableList = dataTit.data;
          this.barLineData = {};
          this.barLineData.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
          this.barLineData.color = ['#40c68c'];
            commit('UPDATE_LOADING', false)
        }else{
            commit('UPDATE_LOADING', false)
          console.log(dataTit.message);
        }
      },
      async getBarLineDataSMD() {
          commit('UPDATE_LOADING', true)
      let id = "APP10102"
      let date=formatData.dateConversion(this.time);
      let dataTit = await getList(id,{date_:date});
        if(dataTit.success){
          //表头：
          //表格:
          const TableList = dataTit.data;
          this.barLineDataSMD = {};
          this.barLineDataSMD.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
          this.barLineDataSMD.color = ['#bb66f2'];
            commit('UPDATE_LOADING', false)
        }else{
            commit('UPDATE_LOADING', false)
          console.log(dataTit.message);
        }
      },
      async getBarLineDataADD() {
          commit('UPDATE_LOADING', true)
      let id = "APP10103"
      let date=formatData.dateConversion(this.time);
      let dataTit = await getList(id,{date_:date});
        if(dataTit.success){
          //表头：
          //表格:
          const TableList = dataTit.data;
          this.barLineDataADD = {};
          this.barLineDataADD.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
          this.barLineDataADD.color = ['#527bef'];
            commit('UPDATE_LOADING', false)
        }else{
            commit('UPDATE_LOADING', false)
          console.log(dataTit.message);
        }
      }
    }, watch: {
    time (newtime, oldtime) {
        this.getBarLineDataADD();
         this.getBarLineDataSMD();
         this.getBarLineData();
          formatData.setDateTime(newtime);
    }
  },
    created: function () {
      this.barLineData = {};
      this.barLineData.dataset = [];
      this.getBarLineData();

      this.barLineDataSMD = {};
      this.barLineDataSMD.dataset = [];
      this.getBarLineDataSMD();

      this.barLineDataADD = {};
      this.barLineDataADD.dataset = [];
      this.getBarLineDataADD();
    }
  }
</script>



